@import "@/assets/global.scss";



























































.question-panel {
  margin-top: 24px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: left;
}

.answer-panel {
  margin-left: 12px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  > label {
    margin: 4px 0;

    > input {
      margin: 1px 0 4px 1px;
      vertical-align: middle;
    }
  }
}

.inner-html {
  > img {
    vertical-align: middle;
  }
}
